import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles, ButtonBase,Box, Tab, Tabs,AppBar } from '@material-ui/core';
import scrollIntoView from 'scroll-into-view';
import TabPanel from '../../components/TabPanel';
import Loadable from '@loadable/component';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import get from 'lodash/get';
import { Link } from 'react-router-dom';
import { withRouter, RouteComponentProps } from 'react-router';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import { RootState } from '../../reducers';
import LinkComponent from '@material-ui/core/Link';
import {
	ChangeSectionAction,
	Content,
	FileLink,
	GetInvestorRelationsContentAction,
	InvestorContent,
	Selections
} from '../../redux/Investors/types';
import Style from './style';
import { Resources } from '../../constants/resources';

const Card = Loadable(() => import('../../components/Card'));
const InvestorRelationsSubMenu = Loadable(() => import('../InvestorRelationsSubMenu'));
const DesktopCard = Loadable(() => import('../../components/DesktopCard'));
const InvestorRelationsCategory = Loadable(() => import('../InvestorRelationsCategory'));

interface Props extends WithStyles<typeof Style>, RouteComponentProps {
	getInvestorRelationsContent: typeof GetInvestorRelationsContentAction.action;
	selections: Selections;
	changeSection: typeof ChangeSectionAction.action;
	isDesktopView:boolean;
}

interface State {
	selectedTab: number;
}
/**
 * InvestorRelations page.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class InvestorRelations extends React.PureComponent<Props,State> {
	constructor(props: Props) {
		super(props);
		this.state = {
			selectedTab: 0
		};
	}
	componentDidMount() {
		const { getInvestorRelationsContent, selections, location, changeSection } = this.props;
		const selectedSection = get(selections, 'section', '');
		// scrollIntoView(document.getElementById('home') as any);
		if (selectedSection === '') {
			getInvestorRelationsContent();
		}
		const { search } = location;
		const url = new URLSearchParams(search);
		const section = url.get('section') || '';
		const menu = url.get('menu') || '';
		const category = url.get('category') || '';
		changeSection({ section, menu, category });
	}

	componentDidUpdate(prevProps: Props) {
		const { selections: prevSelections } = prevProps;
		const { selections, location } = this.props;
		const { search } = location;
		const url = new URLSearchParams(search);
		const section = url.get('section') || '';
		const menu = url.get('menu') || '';
		const category = url.get('category') || '';
		if (prevSelections.section !== selections.section && selections.section !== '') {
			const { history } = this.props;
			history.push(
				`/investor-relationship?section=${
					section === '' ? selections.section : section
				}&menu=${menu}&category=${category}`
			);
		}
	}

	onChangeSection = (e) => {
		const value = get(e, 'target.value', '');
		const { changeSection } = this.props;
		changeSection({ section: value });
	};

	handleTabChange = (event: React.ChangeEvent<any>, newValue: number) => {
		const { changeSection, selections } = this.props;
		const sections: Array<string> = get(selections, 'sections', []);
		const value = sections[newValue] || '';
		changeSection({ section: value });
		this.setState({ selectedTab: newValue });
	};

	onChangeSelections = (e,options, selectedProp) => {
		const value = get(e, 'target.value', '');
		console.log(options,value);
		const { changeSection } = this.props;
		changeSection( {...options,[selectedProp]:value} );
	};
	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const {selectedTab} = this.state;
		const { classes, selections, location, isDesktopView } = this.props;
		const selectedSection = get(selections, 'section', '');
		const sections: Array<string> = get(selections, 'sections', []);
		const submenus: Array<string> = get(selections, 'submenus', []);
		const selectedSubmenu:string = get(selections, 'submenu', '');		
		const selectedSubcategory:string = get(selections, 'subcategory', '');		
		const subcategories: Array<string> = get(selections, 'subcategories', []);
		const showFiles: Array<any> = get(selections, 'showFiles', []);
		const fileLinks: Array<FileLink> = get(selections, 'fileLinks', []);
		const { search } = location;
		const url = new URLSearchParams(search);
		const section = url.get('section') || '';
		const menu = url.get('menu') || '';
		const category = url.get('category') || '';
		const {
			REACT_APP_API_URL = '',
			REACT_APP_CONTENT_BASE_URL = '',
			REACT_APP_FILE_API = ''
		} = process.env;
		const CONTENT_FILE_API =
			REACT_APP_API_URL + REACT_APP_CONTENT_BASE_URL + REACT_APP_FILE_API;
		
		return (
			isDesktopView ? 
			<div className={classes.content}>
				<DesktopCard name="Investor Relations" image="/images/investor-desktop.png" />
				<div className={classes.block}>
							<p
								className={clsx(
									classes.marginTop_8,
									classes.section,
									classes.smallFont
								)}
							>
								{Resources.InvestorRelationsContent}
							</p>
				</div>
				<div className={classes.block}>
					<div className={classes.tabContainer}>
						<AppBar
							position="static"
							color="default"
							className={classes.appBar}
							elevation={0}
						>
							<Tabs
								centered
								className={classes.tabs}
								onChange={this.handleTabChange}
								value={selectedTab}
								aria-label="simple tabs example"
								indicatorColor="primary"
								textColor="primary"
								variant="fullWidth"
								scrollButtons="auto"
								classes={{
									indicator:
										classes.indicator
								}}
								TabIndicatorProps={{ children: <span /> }}
							>
								{sections.map((item, index) => <Tab
									color="primary"
									disableRipple
									label={
										<Box
											className={clsx(classes.tabFont, {
												[classes.activeTab]: selectedTab === index
											})}
										>
											{item}
										</Box>
									}
								/>)
								}
							</Tabs>
						</AppBar>
						<div className={classes.tabpanels}>
							{sections.map((item,index)=>
							<TabPanel value={selectedTab} index={index}>
								<Grid container spacing={1}>
									<Grid item sm={4} md={3}>
										<TextField
											id="section"
											select
											label="Section"
											value={selectedSubmenu}
											onChange={(e)=>this.onChangeSelections(e,{section:item},'menu')}
											variant="outlined"
											SelectProps={{
												displayEmpty: true
											}}
											className={classes.field}
											InputProps={{
												className: classes.padding
											}}
											classes={{ root: classes.outlined }}
										>
											{submenus.map((option) => (
												<MenuItem key={option} value={option}>
													{option}
												</MenuItem>
											))}
										</TextField>
										</Grid>
									{subcategories.length>0 &&
									<Grid item sm={4} md={3}>
											<TextField
											id="section"
											select
											label="Category"
											value={selectedSubcategory}
											onChange={(e)=>this.onChangeSelections(e,{section:selectedSection,menu:selectedSubmenu},'category')}
											variant="outlined"
											SelectProps={{
												displayEmpty: true
											}}
											className={classes.field}
											InputProps={{
												className: classes.padding
											}}
											classes={{ root: classes.outlined }}
										>
											{subcategories.map((option) => (
												<MenuItem key={option} value={option}>
													{option}
												</MenuItem>
											))}
										</TextField>
									</Grid>}
					
					{showFiles.map(({ files, heading }) => (
						<>
							{files.map((file) => (
								<Grid item xs={12} sm={12} key={file.fileId}>
									<div className={classes.linkBody}>
										<PictureAsPdfIcon className={classes.largeFont} />
										<LinkComponent
											className={clsx(classes.link)}
											href={`${CONTENT_FILE_API}/${file.fileId}/${file.fileName}`}
											color="secondary"
											target="__blank__"
										>
											{file.fileName.replace(/(\.pdf)/gi, '')}
										</LinkComponent>
									</div>
								</Grid>
							))}
						</>
					))}
					{
						showFiles.length===0 && 
						<Grid container spacing={1}>
						{/* <Grid
							item
							xs={12}
							sm={12}
							className={clsx(classes.fontSize_18, classes.marginTop_8)}
						>
							{selectedSubcategory}
						</Grid> */}
						{fileLinks.map((file) => (
							<Grid item xs={12} sm={12} key={file.fileId}>
								<div className={classes.linkBody}>
									<PictureAsPdfIcon className={classes.largeFont} />
									<LinkComponent
										className={clsx(classes.link)}
										href={`${CONTENT_FILE_API}/${file.fileId}/${file.fileName}`}
										color="secondary"
										target="__blank__"
									>
										{file.fileName.replace(/(\.pdf)/gi, '')}
									</LinkComponent>
								</div>
							</Grid>
						))}
						</Grid>
					}
								</Grid>
							</TabPanel>)}
						</div>
					</div>
				</div>
			</div> : 
			<div className={classes.content}>
			{(section === '' || menu === '') && (
				<div id="home">
					<Card name="" designation="" image="/images/social.png" smallCard />
				</div>
			)}
			{(section === '' || menu === '') && (
				<div className={classes.block}>
					<div className={classes.largerFont}>{Resources.InvestorRelationsTitle}</div>
					<p className={clsx(classes.section, classes.smallFont)}>
						{Resources.InvestorRelationsContent}
					</p>
					<Grid container spacing={1}>
						<Grid item xs={12} sm={12}>
							<TextField
								id="section"
								select
								label="Section"
								value={selectedSection}
								onChange={this.onChangeSection}
								variant="outlined"
								SelectProps={{
									displayEmpty: true
								}}
								className={classes.field}
								InputProps={{
									className: classes.padding
								}}
								classes={{ root: classes.outlined }}
							>
								{sections.map((option) => (
									<MenuItem key={option} value={option}>
										{option}
									</MenuItem>
								))}
							</TextField>
						</Grid>
					</Grid>
					<Grid container spacing={1}>
						{submenus.map((_menu) => (
							<Grid item xs={12} sm={12} key={_menu}>
								<Link
									to={`/investor-relationship?section=${selectedSection}&menu=${_menu}`}
									className={clsx(classes.link)}
									key={_menu}
								>
									<ButtonBase className={classes.button} disableRipple>
										{_menu}
									</ButtonBase>
								</Link>
							</Grid>
						))}
					</Grid>
				</div>
			)}
			{section !== '' && menu !== '' && category === '' && <InvestorRelationsSubMenu />}
			{section !== '' && menu !== '' && category !== '' && <InvestorRelationsCategory />}
		</div>
			);
	}
}

const mapStateToProps = (state: RootState) => ({
	selections: state.investorRelations.selections,
	isDesktopView: state.screenReducer.isDesktopView
});

const mapDispatchToProps = {
	getInvestorRelationsContent: GetInvestorRelationsContentAction.action,
	changeSection: ChangeSectionAction.action
};

export default withStyles(Style, { name: 'DmartCorpFreshInvestorRelations' })(
	withRouter(connect(mapStateToProps, mapDispatchToProps)(InvestorRelations))
);
